@import url(https://fonts.googleapis.com/css?family=Bungee+Inline&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lalezar&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.bottom-part {
  display: flex;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
  height: 35vmin;
  pointer-events: none;
  margin-bottom: 5vmin;
}

.Epic-logo {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.Image {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

}

.Epic-text {
  font-family: 'Bungee Inline', cursive;
  font-size: 20vmin;
  color: #71DB77;
}

.Parsa {
  font-size: 2vmin;
  font-family: 'Lalezar', cursive;
}

.github-logo {
  height: 5vmin;
  padding-right: 2vmin
}

.github-logo:hover {
  -webkit-animation: github-logo-spin infinite 0.2s linear;
          animation: github-logo-spin infinite 0.2s linear; 
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@-webkit-keyframes github-logo-spin {
  0% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@keyframes github-logo-spin {
  0% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}
